body {
  font-family: Arial, Helvetica, sans-serif;
}
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.container{
  width: 80%;
  margin: 0 auto;
}

.wraper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}

header{
  padding: 10px 0px;
}

.carttop img{
  width: 40px;
}

.wraper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}

.arrowbox {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.arrowbox img{
  margin-right: 10px;
  width: 15px;
}

.cartbox {
  position: relative;
}

.cartbox span{
  position: absolute;
  width: 25px;
  height: 25px;
  background: rgb(211, 0, 147);
  border-radius: 30px;
  color: white;
  display: grid;
  place-items: center;
  top: 5px;
  right: 0;
  padding: 5px;
  font-size: 14px;
  font-weight: 600;
}

section#shoppingSelect {
  margin-top: 20px;
}

section#shoppingSelect p span {
  font-weight: 600;
}

section#shoppingSelect p {
  font-size: 15px;
}

.cartImg{
  width: 120px;
}
.cartImg img{
  max-width: 100%;
  border-radius: 5px;
}

.cartMain{
  background: #f5f5f5;
  margin-top: 20px;
}

.cartInner{
  padding: 30px;
}

.singleCart {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 10px 0px;
  flex-wrap: wrap;
}

.cartTotal{
  text-align: right;
  margin: 10px 0px;
}

button{
  background: none;
  cursor: pointer;
  border: none;
  outline: none;
}
.checkout{
  background: rgb(0, 67, 250);
  color: white;
  border-radius: 5px;
  padding: 10px 15px;
  transition: .3s;
}

.checkout:hover{
  background: #e91363;
}

.clearCart{
  background: rgb(250, 79, 0);
  color: white;
  border-radius: 5px;
  padding: 10px 15px;
  transition: .3s;
  margin-left: 5px;
}

.clearCart:hover{
  background: red;
}

.titleDes {
  width: 18%;
}

.increDecre {
  width: 11%;
}

.increDecre input {
  width: 35px;
  text-align: center;
  padding: 2px;
  outline: none;
}

.increDecre i {
  margin: 0px 5px;
  font-size: 14px;
  cursor: pointer;
}

.delete{
  cursor: pointer;
  color:red;
}

/* media queries */
@media (max-width: 980px){
  .increDecre{
    width: 15%;
  }
  .titleDes{
    width: 20%;
  }
}
@media (max-width: 768px) {
  .cartImg{
    width: 50%;
    margin-bottom: 10px;
  }
  .titleDes{
    width: 50%;
    text-align: center;
  }
  .increDecre{
    width: 30%;
  }
  .price{
    width: 30%;
  }
}
@media (max-width: 406px){
  .increDecre i, .increDecre input{
    display: block;
    text-align: center;
    margin: 0 auto;
  }
  .increDecre i:last-child{
    margin-top: 4px
  }
}